<template>
    <v-container fluid>
        <div v-if="blogArticles.length===0&&!loading">暂无数据</div>
        <div v-else>
            <div v-if="firstLoading">
                <v-timeline align-top :dense="$vuetify.breakpoint.smAndDown">
                    <v-timeline-item v-for="index in [0,1]" :key="index"
                                     :color="index%2===0?'purple':'blue'" fill-dot
                                     :right="index%2===0" :small="index%2===0">
                        <v-card :color="index%2===0?'purple':'blue'" max-height="200px">
                            <v-skeleton-loader class="pa-3" type="heading"/>
                            <v-card tile flat>
                                <v-skeleton-loader type="list-item"/>
                            </v-card>
                        </v-card>
                        <template :slot="$vuetify.breakpoint.smAndDown?'default':'opposite'">
                            <v-skeleton-loader class="pa-1 mt-2" width="150px" type="text"/>
                        </template>
                    </v-timeline-item>
                </v-timeline>
            </div>
            <div v-else>
                <div v-for="(item,index) in blogArticles" :key="index">
                    <h1 class="text-center">{{item.dateString}}</h1>
                    <v-timeline align-top :dense="$vuetify.breakpoint.smAndDown">
                        <v-timeline-item v-for="(blogArticle,articleIndex) in item.blogArticles" :key="blogArticle.id"
                                         :color="articleIndex%2===0?'purple':'blue'" fill-dot
                                         :right="articleIndex%2===0" :small="articleIndex%2===0">
                            <v-hover>
                                <template v-slot:default="{ hover }">
                                    <v-card max-height="200px" :color="articleIndex%2===0?'purple':'blue'">
                                        <v-card-title class="white--text pa-2">
                                            {{blogArticle.title}}
                                        </v-card-title>
                                        <v-card tile flat>
                                            <v-card-text class="pa-2">{{blogArticle.articleAbstract}}</v-card-text>
                                        </v-card>
                                        <v-fade-transition>
                                            <v-overlay v-if="hover" absolute>
                                                <v-btn :color="articleIndex%2===0?'purple':'blue'"
                                                       @click="clickArticle(blogArticle)">
                                                    查看详情
                                                </v-btn>
                                            </v-overlay>
                                        </v-fade-transition>
                                    </v-card>
                                </template>
                            </v-hover>
                            <template :slot="$vuetify.breakpoint.smAndDown?'default':'opposite'">
                                <v-card-text :class="$vuetify.breakpoint.smAndDown?
                            'pa-1 text--secondary':'pa-1 text--primary'">
                                    {{blogArticle.uploadTimeString}}
                                </v-card-text>
                            </template>
                        </v-timeline-item>
                    </v-timeline>
                    <v-divider v-if="index!==blogArticles.length-1"/>
                </div>
            </div>
            <div class="d-block text-center mt-3">
                <div v-if="!firstLoading">
                    <v-progress-circular v-if="loading" indeterminate color="accent"/>
                    <div v-else-if="!isLastPage">
                        <v-btn @click="getArticles">加载更多</v-btn>
                    </div>
                    <div v-else class="mt-2">
                        没有更多数据了
                    </div>
                </div>
            </div>
        </div>
    </v-container>
</template>

<script>
    export default {
        name: "TimeLine",
        data() {
            return {
                currentPage: 1,
                loading: Boolean,
                firstLoading: true,
                isLastPage: false,
                blogArticles: []
            }
        },
        methods: {
            getArticles() {
                this.loading = true
                let _this = this
                this.axios.get(this.API.URL.QUERY_ARTICLE_WITH_TIMELINE + "&size=" + (this.currentPage * this.API.PER_PAGE_COUNT), {headers: {'Content-Type': 'multipart/form-data'}})
                    .then(function (result) {
                        if (_this.firstLoading) {
                            _this.firstLoading = false
                        }
                        _this.loading = false
                        let data = result.data
                        if (data.success) {
                            _this.isLastPage = data.data.isLastPage
                            let keys = Object.keys(data.data.data)
                            _this.blogArticles = []
                            for (let i = 0; i < keys.length; i++) {
                                let key = keys[i]
                                let value = data.data.data[key]
                                _this.blogArticles.push({dateString: key, blogArticles: value})
                            }
                            if (!data.data.isLastPage) {
                                _this.currentPage++
                                console.log("_this.blogArticles", _this.blogArticles)
                            }
                        }
                    }, function (error) {
                        _this.loading = false
                        console.log(error)
                    })
            },
            clickArticle: function (blogArticle) {
                //path不能和params连用
                this.$router.push({
                    name: "article",
                    params: {id: blogArticle.id, articleFromPre: blogArticle}
                })
            }
        },
        mounted() {
            this.getArticles()
        }
    }
</script>

